:root {
  --bg: #fcf0d9;
  --dark: #272622;
  --blue: #018cc5;
  --green: #8abb3b;
  --yellow: #fee800;
  --orange: #f87621;
  --pink: #f43e8b;
}

.masonry-list {
  left: 50%;
  margin: -320px 0 0 -480px;
  position: absolute;
  top: 92%;
  width: 960px;
}
.masonry-list li {
  float: left;
  height: 320px;
  width: 100%;
  overflow: hidden;
  padding: 2 1px 1px 0;
  width: 320px;
}
/* .masonry-list li:focus a::after,
.masonry-list li:hover a::after {
  opacity: 1;
  -moz-transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
} */
.masonry-list li:focus .tile-primary-content img,
.masonry-list li:hover .tile-primary-content img {
  transform: scale3d(1.2, 1.2, 0);
  transform-origin: 50% 50%;
  transition: transform 15000ms cubic-bezier(0, 0, 0.8, 0.9);
}

.masonry-list li:hover p,
.masonry-list li:hover h2,
.masonry-list li:hover .tile-secondary-content {
  opacity: 1;
}
.masonry-list li a::after {
  background-image: url('data:image/png;base64,');
  bottom: 40px;
  content: '';
  display: block;
  height: 17px;
  opacity: 0;
  position: absolute;
  right: 40px;
  width: 17px;
  transform: translate3d(-10px, -10px, 0);
  transition: all 150ms ease-out;
}

.masonry-list li.tile-twitter:focus .tile-primary-content p,
.masonry-list li.tile-twitter:hover .tile-primary-content p {
  transform: translate3d(150%, 0, 0);
  transition: transform 300ms cubic-bezier(0, 0, 0.4, 1);
}

.masonry-list li.tile-twitter:focus .tile-secondary-content p,
.masonry-list li.tile-twitter:hover .tile-secondary-content p {
  transform: translate3d(0, 0, 0);
  -moz-transition: -moz-transform 300ms cubic-bezier(0, 0, 0.4, 1) 150ms;
  -o-transition: -o-transform 300ms cubic-bezier(0, 0, 0.4, 1) 150ms;
  -webkit-transition: -webkit-transform 300ms cubic-bezier(0, 0, 0.4, 1);
  -webkit-transition-delay: 150ms;
  transition: transform 300ms cubic-bezier(0, 0, 0.4, 1) 150ms;
}

.masonry-list li.tile-twitter .tile-primary-content p {
  transform: translate3d(0, 0, 0);
  -moz-transition: -moz-transform 300ms cubic-bezier(0, 0, 0.4, 1) 150ms;
  -o-transition: -o-transform 300ms cubic-bezier(0, 0, 0.4, 1) 150ms;
  -webkit-transition: -webkit-transform 300ms cubic-bezier(0, 0, 0.4, 1);
  -webkit-transition-delay: 150ms;
  transition: transform 300ms cubic-bezier(0, 0, 0.4, 1) 150ms;
}

.masonry-list li.tile-twitter .tile-secondary-content p {
  opacity: 1;
  transform: translate3d(-150%, 0, 0);
  transition: transform 300ms cubic-bezier(0, 0, 0.4, 1);
}
.masonry-list li.tile-job .tile-primary-content {
  background-color: #71964f;
}
.masonry-list li.tile-facebook a::after {
  background-image: url('data:image/png;base64,');
  background-repeat: no-repeat;
  height: 45px;
  opacity: 1;
  width: 22px;
  transform: translate3d(0, 0, 0);
}

.masonry-list li.tile-twitter a::after {
  background-image: url('data:image/png;base64,');
  background-repeat: no-repeat;
  height: 37px;
  opacity: 1;
  width: 45px;
  transform: translate3d(0, 0, 0);
}
.masonry-list li.tile-twitter .tile-primary-content {
  background-color: #00afe0;
}
.masonry-list li.tile-case .tile-primary-content,
.masonry-list li.tile-client .tile-primary-content,
.masonry-list li.tile-person .tile-primary-content {
  padding: 0;
}
.masonry-list li.tile-case .tile-secondary-content,
.masonry-list li.tile-client .tile-secondary-content,
.masonry-list li.tile-person .tile-secondary-content {
  background-color: rgba(0, 0, 0, 0.7);
  transition: opacity 100ms ease-out;
}
.masonry-list li.tile-client a::after {
  background-image: url('data:image/png;base64,');
  height: 22px;
  width: 23px;
  transform: translate3d(0, 0, 0);
}
.masonry-list li.tile-client a:focus .tile-primary-content,
.masonry-list li.tile-client a:hover .tile-primary-content {
  background-color: transparent;
}
.masonry-list li.tile-client a:focus .tile-primary-content img,
.masonry-list li.tile-client a:hover .tile-primary-content img {
  transform: scale3d(1, 1, 1);
}
.masonry-list li.tile-client .tile-primary-content {
  background-color: #604951;
  transition: background-color 250ms ease-out;
}
.masonry-list li.tile-person {
  text-align: center;
}
.masonry-list li.tile-person .tile-secondary-content {
  display: table;
  height: 100%;
  padding: 0;
  width: 100%;
}
.masonry-list li.tile-person .tile-secondary-container {
  display: table-cell;
  vertical-align: middle;
}
.masonry-list li.tile-person h2 {
  font-size: 24px;
  position: static;
}
.masonry-list li.tile-person p {
  color: #a18e5c;
  font-family: 'Georgia', serif;
  font-size: 17px;
  margin: 0.2em;
  text-transform: none;
}
.masonry-list a,
.masonry-list button {
  color: white;
  display: block;
  height: 100%;
  position: relative;
  width: 100%;
}
.masonry-list .tile-primary-content,
.masonry-list .tile-secondary-content {
  bottom: 0;
  left: 0;
  overflow: hidden;
  padding: 40px;
  position: absolute;
  right: 0;
  top: 0;
}
.masonry-list .tile-primary-content {
  background-color: #a49058;
}
.masonry-list .tile-primary-content img {
  width: 100%;
  transform: scale3d(1, 1, 1);
  transition: transform 200ms ease-out;
}
.masonry-list .tile-secondary-content {
  opacity: 0;
  transition: opacity 250ms ease-out;
}
.masonry-list .tile-secondary-content p,
.masonry-list .tile-secondary-content h2 {
  opacity: 0;
  -moz-transition: opacity 200ms ease-in 100ms;
  -o-transition: opacity 200ms ease-in 100ms;
  -webkit-transition: opacity 200ms ease-in;
  -webkit-transition-delay: 100ms;
  transition: opacity 200ms ease-in 100ms;
}
.masonry-list p {
}
.masonry-list h2 {
  text-transform: uppercase;
}
.masonry-list h2 {
  font-size: 12px;
  margin: 0;
  position: absolute;
}
.masonry-list p {
  font-size: 20px;
  font-weight: 300;
  line-height: 1.1em;
  margin: 1em 0 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

* {
  -webkit-font-smoothing: antialiased;
}

body {
  color: #272622;
  color: var(--dark);
  background-color: #fcf0d9;
  background-color: var(--bg);
  font-family: 'Source Sans Pro', sans-serif;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.fetchTweetsButton {
  font-size: 2rem;
  margin-left: -0.25rem;
  text-transform: uppercase; }

.pageHeading {
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 1rem;
  line-height: 1.25rem;
  margin-left: 0.25rem;
  font-weight: normal; }

body {
  margin: 0;
  font-family: Helvetica;
  color: var(--dark);
  background-color: var(--bg);
  padding: 2rem;
  position: relative;
  box-shadow: 10px 0 3px -5px rgba(255, 255, 255, 0.25) inset;
  min-height: 75vh; }

body,
input {
  color: #28283d;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

button,
input[type='submit'] {
  width: 100%;
  padding: 1em;
  font-family: 'Dimitri Swank';
  cursor: pointer;
  border: 1px solid black;
  color: #8abb3b;
  background-color: black;
  outline: none;
  border: none; }

.Application {
  margin: 0.5em auto;
  max-width: 300px; }
  .Application h1 {
    text-align: center; }

